.chat-records-wrapper {
  padding: 35px 60px;
}
.chat-records-wrapper .message {
  margin-bottom: 20px;
  line-height: 1.5;
  font-weight: 400;
  text-align: left;
}
.chat-records-wrapper .message .message-wrapper .message-content {
  display: inline-block;
  max-width: 400px;
  margin-left: 20px;
  padding: 16px 36px;
  background-color: #e7e8e8;
  border-radius: 18px;
  text-align: left;
  word-break: break-all;
}
.chat-records-wrapper .message .message-wrapper .message-content h6 {
  color: #343a40;
  font-size: 16px;
  font-weight: 600;
}
.chat-records-wrapper .message .message-wrapper .message-content span {
  color: #495057;
}
.chat-records-wrapper .message .message-options {
  display: inline-flex;
  align-items: center;
  margin-top: 5px;
  color: #adb5bd;
}
.chat-records-wrapper .message .message-options .avatar {
  position: relative;
  display: inline-block;
  width: 36px;
  height: 36px;
  margin: -18px 5px 0;
  vertical-align: bottom;
}
.chat-records-wrapper .message .message-options .avatar img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  box-shadow: 0 0 0 8px #fff;
}
.chat-records-wrapper .message .message-options .message-date {
  height: 1.125rem;
  line-height: 1.125rem;
  display: inline-block;
  margin: 0 5px;
  font-size: 12px;
  vertical-align: middle;
}
.chat-records-wrapper .message.self {
  text-align: right;
}
.chat-records-wrapper .message.self .message-wrapper .message-content {
  background-color: #8500ff;
  color: #fff;
  margin-right: 20px;
  margin-left: 0;
}
.chat-records-wrapper .message.self .message-wrapper .message-content span {
  color: #fff;
}
.chat-records-wrapper .message.self .message-options {
  flex-direction: row-reverse;
}
