.chat-to-header-wrapper {
  border-bottom: 1px solid #e5e9f2;
  overflow: hidden;
  padding: 12px 36px;
}
.chat-to-header-wrapper .chat-info {
  display: flex;
  align-items: center;
}
.chat-to-header-wrapper .chat-info .avatar {
  width: 48px;
  height: 48px;
}
.chat-to-header-wrapper .chat-info .avatar img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.chat-to-header-wrapper .chat-info .chat-name {
  margin-left: 10px;
  font-size: 16px;
  font-weight: bold;
}
