.chat-content-wrapper {
  display: flex;
  flex-grow: 1;
  flex-flow: column;
}
.chat-content-wrapper .chat-content {
  flex: 2 1;
  overflow-y: auto;
  overflow-x: hidden;
}
.chat-content-wrapper .chat-content-null {
  display: flex;
  flex: 2 1;
  align-items: center;
  justify-content: center;
  font-size: 22px;
}
.chat-content-wrapper .chat-footer {
  height: 80px;
}
