.chat-send-wrapper {
  padding: 12px 36px;
  border-top: 1px solid #e5e9f2;
}
.chat-send-wrapper .form-row {
  display: flex;
  align-items: center;
}
.chat-send-wrapper .form-row .ant-form-item {
  margin: 0;
}
.chat-send-wrapper .form-row .input-send {
  flex-basis: 0;
  flex-grow: 1;
  padding: 0 5px;
}
.chat-send-wrapper .form-row .input-send .send-area {
  height: 38px;
  padding: 6px 12px;
  font-size: 16px;
  border: none;
  resize: none;
}
.chat-send-wrapper .form-row .input-send .send-area:focus {
  border: none;
  box-shadow: none;
}
.chat-send-wrapper .form-row .btn-send {
  width: 48px;
  height: 48px;
  padding: 0 5px;
  border: none;
  border-radius: 50%;
  background-color: #8500ff;
  font-size: 22px;
  color: #fff;
  cursor: pointer;
  transition: 0.3s;
}
.chat-send-wrapper .form-row .btn-send:hover {
  background-color: #3535f5;
}
